.Search{
    width: 50%;
    margin: 0 auto;
    text-align: center;
    padding: 50px 0;
}

.Search > input{
    height: 40px;
    width: 40%;
    border-radius: 10px;
    font-size: 20px;
    
}

@media screen and (max-width: 510px) {
    .Search{
        width: 80%;
      }
    .Search > input{
      width: 100%;
    }
  }